
"use client";

import * as React from 'react';
import { PageHeader } from "@/components/page-header";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import type { Item, Category } from "@/types";

export default function ReportsPage() {
    const [items, setItems] = React.useState<Item[]>([]);
    const [categories, setCategories] = React.useState<Category[]>([]);
    const [isClient, setIsClient] = React.useState(false);

    const loadData = () => {
        try {
            const storedItems = localStorage.getItem('inventopro-items');
            if (storedItems) {
                setItems(JSON.parse(storedItems));
            }
            const storedCategories = localStorage.getItem('inventopro-categories');
            if (storedCategories) {
                setCategories(JSON.parse(storedCategories));
            }
        } catch (error) {
            console.error("Failed to parse from localStorage", error);
        }
    };
    
    React.useEffect(() => {
        setIsClient(true);
        loadData();

        const handleStorageChange = (event: StorageEvent) => {
            if (event.key === 'inventopro-items' || event.key === 'inventopro-categories') {
                loadData();
            }
        };

        window.addEventListener('storage', handleStorageChange);

        return () => {
            window.removeEventListener('storage', handleStorageChange);
        };
    }, []);

    const lowStockItems = items.filter(item => item.quantity === 0);

    if (!isClient) {
        return (
            <>
                <PageHeader
                    title="Reports"
                    description="Generate and view inventory reports."
                />
                <div className="grid gap-6">
                    <Card>
                        <CardHeader>
                            <CardTitle>Low Stock Report</CardTitle>
                            <CardDescription>Items that are at or below their re-order point.</CardDescription>
                        </CardHeader>
                        <CardContent>
                           <p>Loading...</p>
                        </CardContent>
                    </Card>
                    <Card>
                        <CardHeader>
                            <CardTitle>Full Inventory Report</CardTitle>
                            <CardDescription>A complete list of all items in your inventory.</CardDescription>
                        </CardHeader>
                        <CardContent>
                            <p>Loading...</p>
                        </CardContent>
                    </Card>
                </div>
            </>
        )
    }

    return (
        <>
            <PageHeader
                title="Reports"
                description="Generate and view inventory reports."
            />
            <div className="grid gap-6">
                <Card>
                    <CardHeader>
                        <CardTitle>Low Stock Report</CardTitle>
                        <CardDescription>Items that have a quantity of 0.</CardDescription>
                    </CardHeader>
                    <CardContent>
                        <Table>
                            <TableHeader>
                                <TableRow>
                                    <TableHead>Item</TableHead>
                                    <TableHead>SKU</TableHead>
                                    <TableHead className="text-right">Quantity</TableHead>
                                    <TableHead className="text-right">Re-order Point</TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                                {lowStockItems.map(item => (
                                    <TableRow key={item.id}>
                                        <TableCell className="font-medium">{item.name}</TableCell>
                                        <TableCell>{item.sku}</TableCell>
                                        <TableCell className="text-right">{item.quantity}</TableCell>
                                        <TableCell className="text-right">{item.reorderPoint}</TableCell>
                                    </TableRow>
                                ))}
                                {lowStockItems.length === 0 && (
                                     <TableRow>
                                        <TableCell colSpan={4} className="text-center">
                                            No items are currently low on stock.
                                        </TableCell>
                                    </TableRow>
                                )}
                            </TableBody>
                        </Table>
                    </CardContent>
                </Card>
                <Card>
                    <CardHeader>
                        <CardTitle>Full Inventory Report</CardTitle>
                        <CardDescription>A complete list of all items in your inventory.</CardDescription>
                    </CardHeader>
                    <CardContent>
                    <Table>
                            <TableHeader>
                                <TableRow>
                                    <TableHead>Item</TableHead>
                                    <TableHead>Category</TableHead>
                                    <TableHead className="text-right">Quantity</TableHead>
                                    <TableHead className="text-right">Price</TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                                {items.map(item => (
                                    <TableRow key={item.id}>
                                        <TableCell className="font-medium">{item.name}</TableCell>
                                        <TableCell>{categories.find(c => c.id === item.categoryId)?.name || 'N/A'}</TableCell>
                                        <TableCell className="text-right">{item.quantity}</TableCell>
                                        <TableCell className="text-right">${item.price.toFixed(2)}</TableCell>
                                    </TableRow>
                                ))}
                                {items.length === 0 && (
                                     <TableRow>
                                        <TableCell colSpan={4} className="text-center">
                                            No items in inventory.
                                        </TableCell>
                                    </TableRow>
                                )}
                            </TableBody>
                        </Table>
                    </CardContent>
                </Card>
            </div>
        </>
    );
}
